class Editor extends ClassicEditor {}

// Plugins to include in the build.
Editor.builtinPlugins = [
    Alignment,
    Autoformat,
    BlockQuote,
    Bold,
    CKFinderUploadAdapter,
    CloudServices,
    Code,
    CodeBlock,
    Essentials,
    FontBackgroundColor,
    FontColor,
    Heading,
    Image,
    Indent,
    Italic,
    Link,
    List,
    MediaEmbed,
    Paragraph,
    PasteFromOffice,
    Table,
    TableToolbar,
    TextTransformation,
    Underline
];

export default Editor;

ClassicEditor
    .create(document.querySelector('#simple-editor'), {
        toolbar: ['bold', 'italic', 'link', 'bulletedList', 'numberedList', '|', 'table']
    })
    .then(editor => {
        console.log(editor);
    })
    .catch(error => {
        console.error(error);
    });

console.log(ClassicEditor.builtinPlugins.map(plugin => plugin.pluginName))